div.ant-layout {
	background: url("../static/img/bg.jpg") left top no-repeat;
	background-size:100% 100%;
}
/*common component*/

.ant-layout-header {
	background: rgba(0, 0, 0, 0.3);
	overflow: hidden;
}

.common_header_title {
	float: left;
	color: #fff;
}

.common_header_btn, .loginSucc_status {
	float: right;
}

.loginSucc_status {
	color: #fff;
}

.common_header_btn li, .loginSucc_status li {
	float: left;
	margin-left: 30px;
}

.contentWrapper {
	height: 540px;
	overflow: auto;
}

.ant-layout-footer {
	text-align: center;
	height: 30px;
	line-height: 30px;
	padding: 0;
	background: #f1f1f1;
	color: #666;
	font-size: 12px;
}



