.register_Wrapper {
	overflow: hidden;
}

.register_content_left {
	float: left;
	width: 45%;
	height: 400px;
	margin: 40px 6% 40px 2%;
	background: url("../../static/img/left2.jpg") left top no-repeat;
	background-size: 100% 100%;
	border-radius: 10px;
}

.register_content_right {
	float: left;
	margin:10px 0;
	width: 40%;
	padding: 5px;
	border-radius: 10px;
	background: rgba(0,0,0,0.3);
	position: relative;
}

.register_content_right h3 {
	width: 100%;
	text-align: center;
	height: 35px;
	line-height: 35px;
	font-size: 16px;
	color: #fff;
}

.register_content_right ul{
	width: 80%;
	margin: 0 auto;
}

.register_content_right ul li {
	width: 100%;
	margin-bottom: 15px;
}

.register_content_right ul li {
	margin-bottom: 10px;
}

.register_content_right ul li:last-child {
	margin-bottom: 0;
}

.register_content_right ul li label {
	display: inline-block;
	width: 15%;
	text-align: right;
	font-size: 12px;
	color: #fff;
	margin-right: 10px;
}

.register_content_right ul li input  {
	width: 70%;
	height: 25px;
	background: #fff;
	border: none;
	color: #333;
	font-size: 12px;
	padding-left: 3%;
}

.register_content_right ul li textarea,.register_content_right ul li select {
	font-size: 12px;
}

.register_content_right ul li:nth-child(8) {
	margin-top: 25px;
}

.register_content_right ul li:nth-child(8) button {
	margin: 0 15px;
}


