}

.register_tips {
	position: absolute;
	top: 5px;
	left: 12%;
	width: 75%;
	height: 30px;
	line-height: 30px;
	color: #333;
	font-size: 14px;
	text-align: center;
	-webkit-box-shadow: 0px 0px 3px #f1f1f1;
	        box-shadow: 0px 0px 3px #f1f1f1;
	border-radius: 3px;
	display: none;
}

.register_usertype {
	width: 70%;
	height: 35px;
	background: #fff;
	border: none;
	color: #666;
	font-size: 14px;
	padding-left: 4%;
}

.success_tips {
	display: block;
	background: rgba(66, 234, 21, 0.5);
	color: #fff;
}

.error_tips {
	display: block;
	background: rgba(255, 0, 0, 0.5);
	color: #fff;
}



