.login_wrapper {
	overflow: hidden;
	height: 479px;
}

.login_content_left {
	float: left;
	width: 45%;
	height: 350px;
	margin: 50px 6% 50px 2%;
	background: url("../../static/img/left1.jpg") left top no-repeat;
	background-size: 100% 100%;
	border-radius: 10px;
}

.login_content_right {
	float: left;
	width: 35%;
	margin-top: 60px;
	padding: 15px 10px;
	border-radius: 10px;
	background: rgba(0,0,0,0.3);
	position: relative;
}

.login_content_right h3 {
	width: 100%;
	text-align: center;
	height: 66px;
	line-height: 60px;
	font-size: 25px;
	color: #fff;
}

.login_content_right ul {
	width: 80%;
	margin: 0 auto;
}

.login_content_right ul li {
	width: 100%;
	margin-bottom: 15px;
}

.login_content_right ul li:last-child {
	margin-bottom: 0;
}

.login_content_right ul li label {
	display: inline-block;
	width: 20%;
	text-align: right;
	font-size: 14px;
	color: #fff;
	margin-right: 20px;
}

.login_content_right ul li input {
	width: 70%;
	height: 35px;
	background: #fff;
	border: none;
	color: #333;
	font-size: 14px;
	padding-left: 5%;
}

.login_content_right ul li:nth-child(4){
	margin-top: 45px;
}

.login_content_right ul li:nth-child(4)>button {
	margin: 0 15px;
}

.login_tips {
	position: absolute;
	top: 5px;
	left: 12%;
	width: 75%;
	height: 30px;
	line-height: 30px;
	color: #333;
	font-size: 14px;
	text-align: center;
	-webkit-box-shadow: 0px 0px 3px #f1f1f1;
	        box-shadow: 0px 0px 3px #f1f1f1;
	border-radius: 3px;
	display: none;
}

.login_usertype {
	width: 70%;
	height: 35px;
	background: #fff;
	border: none;
	color: #666;
	font-size: 14px;
	padding-left: 4%;
}

.success_tips {
	display: block;
	background: rgba(66, 234, 21, 0.5);
	color: #fff;
}

.error_tips {
	display: block;
	background: rgba(255, 0, 0, 0.5);
	color: #fff;
}


