/*修改个人信息*/
.Medical_UpdateMess_wrapper {
	width: 96%;
	height: 96%;
	padding: 2%;
	background: rgba(36, 68, 233, .1);
}

.Medical_UpdateMess_wrapper ul {
	width: 100%;
}

.Medical_UpdateMess_wrapper ul li {
	width: 100%;
	margin: 2% 0;
}

.Medical_UpdateMess_wrapper ul li label {
	display: inline-block;
	width: 10%;
	height: 45px;
	line-height: 45px;
	font-size: 12px;
	color: #333;
	text-align: right;
}

.Medical_UpdateMess_wrapper ul li input[type='text'],
.Medical_UpdateMess_wrapper ul li input[type='password'],
.Medical_UpdateMess_wrapper ul li input[type='phone'],
.Medical_UpdateMess_wrapper ul li input[type='email'],
.Medical_UpdateMess_wrapper ul li textarea {
	width: 50%;
	height: 40px;
	line-height: 40px;
	padding-left: 1%;
	border: none;
	background: #fff;
	color: #333;
	margin-left: 3%;
}

.Medical_UpdateMess_wrapper ul li textarea {
	height: 60px;
}

.M_UpdateSaveBtn {
	width: 30%;
	height: 40px;
	background: skyblue;
	color: #fff;
	border: none;
	border-radius: 5px;
	font-size: 20px;
	margin-left: 30%;
}

.Medical_showTips {
	display: none;
	width: 50%;
	height: 50px;
	line-height: 50px;
	font-size: 26px;
	color: #fff;
	background: rgba(13, 170, 249, 0.5);
	text-align: center;
	position: absolute;
	top: 50%;
	left: 50%;
	margin-left: -25%;
	margin-top: -25px;
}

/*体检信息录入界面*/
.Examinate_entingWrapper {
	width: 80%;
	margin: 0 auto;
	border: 1px solid #ccc;
	position: relative;
}

.Examinate_entingWrapper h3 {
	height: 35px;
	line-height: 35px;
	background: #f5f5f5;
	color: #666;
	text-align: center;
	font-size: 22px;
	font-family: cursive;
}

.enteringMess {
	width: 80%;
	margin: 15px auto;
}

.enteringMess li {
	height: 45px;
	font-size: 12px;
}

.enteringMess li span {
	display: inline-block;
	width: 20%;
	text-align: right;
	margin-right: 2%;
}

.enteringMess li input, .enteringMess li select {
	display: inline-block;
	width: 50%;
	border: none;
	background: #f1f1f1;
	color: #999;
	height: 35px;
	padding-left: 2%;
}

.enteringMess li input[type=button] {
	width: 15%;
	height: 30px;
	background: skyblue;
	color: #fff;
	border: none;
	border-radius: 5px;
	margin: 15px 30%;
}

/*提示框*/
.ExaminateEntering_Tips {
	display: none;
	width: 50%;
	height: 45px;
	line-height: 45px;
	position: absolute;
	top: 40%;
	left: 23%;
	font-size: 16px;
	text-align: center;
	z-index: 16;
}
.success_tips {
	display: block;
	background: rgba(17, 234, 33, 0.5);
}

.error_tips {
	display: block;
	background: rgba(255, 0, 0, 0.5);
}

/*体检明细查看*/
.checkTestdetailWrapper {
	width: 100%;
	height: 100%;
}

.checkTestDetail_Search {
	width: 100%;
	height: 50px;
	overflow: hidden;
	margin-bottom: 2%;
}

.checkTestDetail_Search input {
	float: left;
	border: none;
	margin-top: 5px;
}

.checkTestDetail_Search input[type=text] {
	width: 30%;
	height: 35px;
	background: #f5f5f5;
	border-radius: 5px 0 0 5px;
	margin-left: 6%;
	padding-left: 2%;
	color: #999;
}

.checkTestDetail_Search input[type=button] {
	width: 6%;
	height: 35px;
	background: skyblue;
	color: #fff;
	border-radius: 0 5px 5px 0;
}


.checkTestDetail_Content ul {
	border: 1px solid #ccc;
}

.checkTestDetail_Content ul li {
	border-bottom: 1px solid #ccc;
	height: 35px;
	line-height: 35px;
	font-size: 12px;
	color: #666;
}

.checkTestDetail_Content ul li:last-child {
	border-bottom: none;
}

.checkTestDetail_Content ul li:first-child {
	height: 40px;
	line-height: 40px;
	font-size: 14px;
	color: #333;
	background: #f5f5f5;
}

.checkTestDetail_Content ul li span {
	display: inline-block;
	width: 12.5%;
	border-right: 1px solid #ccc;
	text-align: center;
}

.checkTestDetail_Content ul li span:last-child {
	border-right: none;
}

.checkTestDetail_Content ul li span:last-child button {
	width: 60px;
	height: 25px;
	line-height: 25px;
	color: #fff;
	background: skyblue;
	border: none;
} 

.mask_glass {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(0, 0, 0, 0.1);
	z-index: 5;
}

.updateTestDetail_window {
	display: none;
	position: absolute;
	top: 10%;
	width: 55%;
	background: #fff;
	border-radius: 10px;
	z-index: 10;
	padding-bottom: 1%;
}

.updateTestDetail_window h3 {
	width: 100%;
	height: 45px;
	background: skyblue;
	color: #fff;
	overflow: hidden;
	line-height: 45px;
}

.updateTestDetail_window h3 span {
	float: left;
	margin-left: 40%;
	font-size: 20px;
}

.updateTestDetail_window h3 b {
	float: right;
	width: 50px;
	height: 25px;
	line-height: 25px;
	background: #f00;
	margin:10px 2%;
	text-align: center;
	cursor: pointer;
}
.updateTestDetail_window ul {
	margin-top: 10px;
}

.updateTestDetail_window ul li {
	height: 50px;
}

.updateTestDetail_window ul li span {
	display: inline-block;
	width: 30%;
	text-align: right;
	margin-right: 2%;
}


.updateTestDetail_window ul li input, .updateTestDetail_window ul li select {
	display: inline-block;
	width: 40%;
	height: 35px;
	background: #f5f5f5;
	border: none;
	padding-left: 2%;
}

.updateTestDetail_window ul li input[type=button] {
	width: 15%;
	height: 40px;
	background: skyblue;
	color: #fff;
	text-align: center;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	font-size: 16px;
	margin:3% 0 0 45%;
	
}

/*预约信息管理*/
.manageorderMess_wrapper {
	width: 90%;
	margin: 0 auto;
}

.manageorderMess_wrapper ul {
	border: 1px solid #ccc;
}

.manageorderMess_wrapper ul li {
	height: 35px;
	line-height: 35px;
	color: #666;
	font-size: 12px;
	border-bottom: 1px solid #ccc;
}

.manageorderMess_wrapper ul li:last-child {
	border-bottom: none;
}

.manageorderMess_wrapper ul li:first-child {
	height: 40px;
	line-height: 40px;
	background: skyblue;
	color: #fff;
	font-size: 14px;
}

.manageorderMess_wrapper ul li span {
	display: inline-block;
	width: 14%;
	text-align: center;
	border-right: 1px solid #ccc;
}

.manageorderMess_wrapper ul li span:last-child {
	border-right: none;
}

.manageorderMess_wrapper ul li span button {
	width: 35%;
	height: 25px;
	text-align: center;
	line-height: 25px;
	background: skyblue;
	color: #fff;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	margin: 0 2% 0 2%;
}





