.System_showTips {
	display: none;
	width: 70%;
	height: 40px;
	line-height: 40px;
	font-size: 20px;
	color: #fff;
	background: rgba(13, 170, 249, 0.7);
	text-align: center;
	position: absolute;
	top: 50%;
	left: 40%;
	margin-left: -25%;
	margin-top: -20px;
	z-index: 16;
}

.addsystemWrapper,
.addmedicalWrapper,
.addworkerWrapper {
	width:60%;
	margin: 5% auto;
	position: relative;
	border: 1px solid #ccc;
}

.addsystemWrapper>h3,
.addmedicalWrapper>h3,
.addworkerWrapper>h3 {
	width: 100%;
	height: 45px;
	line-height: 45px;
	text-align: center;
	font-size: 22px;
	color: #fff;
	background: skyblue;
	margin-bottom: 3%;
}

.addsystemWrapper ul,
.addmedicalWrapper ul,
.addworkerWrapper ul {
	width: 100%;
	height: 100%;
}

.addsystemWrapper ul li,
.addmedicalWrapper ul li,
.addworkerWrapper ul li {
	height: 45px;
	font-size: 12px;
	margin-bottom: 2%;
}

.addsystemWrapper ul li label,
.addmedicalWrapper ul li label,
.addworkerWrapper ul li label {
	display: inline-block;
	width: 30%;
	color: #333;
	text-align: right;
	margin-right: 3%;
}

.addsystemWrapper ul li input,
.addmedicalWrapper ul li input,
.addworkerWrapper ul li input {
	display: inline-block;
	width: 40%;
	height: 35px;
	border: none;
	background: #f5f5f5;
	color: #666;
	padding-left: 2%;
}

.addsystemWrapper ul li input.System_addUser_savebtn,
.addmedicalWrapper ul li input.System_addUser_savebtn,
.addworkerWrapper ul li input.System_addUser_savebtn {
	width: 20%;
	height: 35px;
	background: skyblue;
	color: #fff;
	border-radius: 5px;
	-webkit-box-shadow: 0 0 3px skyblue;
	        box-shadow: 0 0 3px skyblue;
	margin:2% 0 0 40%;
}

/*系统管理员管理系统管理员, 医务人员，教职工信息界面*/
.manageSystem_Wrapper, 
.manageMedical_Wrapper,
.manageWorker_Wrapper {
	width: 90%;
	margin: 5px auto;
	position: relative;
	padding: 0 2%;
}

.System_Mess, 
.Medical_Mess,
.Worker_Mess {
	width: 100%;
	border: 1px solid #ccc;
}

.System_Mess li, 
.Medical_Mess li,
.Worker_Mess li {
	border-bottom: 1px solid #ccc;
	height: 35px;
	line-height: 35px;
	text-align: center;
	color: #333;
	font-size: 12px;
}

.System_Mess li:first-child, 
.Medical_Mess li:first-child,
.Worker_Mess li:first-child {
	font-size: 14px;
	font-weight: bold;
	background: #f1f1f1;
	font-family: "微软雅黑";
}

.System_Mess li:last-child, 
.Medical_Mess li:last-child,
.Worker_Mess li:last-child {
	border-bottom: none;
}

.System_Mess li span, 
.Medical_Mess li span,
.Worker_Mess li span {
	display: inline-block;
	width: 25%;
	border-right: 1px solid #ccc;
}

.System_Mess li span:last-child, 
.Medical_Mess li span:last-child,
.Worker_Mess li span:last-child {
	border-right: none;
}

.System_Mess li span:last-child input[type=button],
.Medical_Mess li span:last-child input[type=button],
.Worker_Mess li span:last-child input[type=button] {
	width: 25%;
	height: 25px;
	line-height: 25px;
	background: deepskyblue;
	cursor: pointer;
	color: #fff;
	border: none;
	-webkit-box-shadow: 5px;
	        box-shadow: 5px;
	border-radius:0 0 3px #000 ;
}

.System_Mess li span:last-child input[type=button]:first-child,
.Medical_Mess li span:last-child input[type=button]:first-child,
.Worker_Mess li span:last-child input[type=button]:first-child {
	margin-right: 15%;
}

.mask_layer {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(0, 0, 0, 0.1);
	z-index: 3;
}

.updateUserWrapper {
	display: none;
	width: 60%;
	height: 300px;
	position: absolute;
	left: 50%;
	top: 45%;
	margin-left: -30%;
}




.updateUserWrapper ul {
	position: absolute;
	z-index: 6;
	width: 100%;
	height: 100%;
	background: #fff;
	border: 1px solid #ccc;
	border-radius: 5px;
	-webkit-box-shadow: 0 0 3px skyblue;
	        box-shadow: 0 0 3px skyblue;
}
.updateUserWrapper ul li {
	height: 35px;
	font-size: 12px;
	color: #333;
	margin-bottom: 5%;
}

.updateUserWrapper ul li > h3 {
	background: #f1f1f1;
	line-height: 35px;
	width: 100%;
	color: #333;
	font-size: 20px;
}

.updateUserWrapper ul li > h3 span {
	display: inline-block;
	width: 90%;
	text-align: center;
}

.updateUserWrapper ul li > h3 button {
	display: inline-block;
	width: 6%;
	height: 30px;
	line-height: 22px;
	background: rgba(255, 0, 0, .3);
	color: #fff;
	border: none;
}

.updateUserWrapper ul li label{
	display: inline-block;
	width: 20%;
	text-align: right;
	margin-right: 5%;
}

.updateUserWrapper ul li input {
	display: inline-block;
	width: 55%;
	height: 35px;
	background: #f5f5f5;
	border: none;
	padding-left: 3%;
}


.updateUserWrapper ul li input.Save_UpdateMess_Btn {
	width: 30%;
	height: 30px;
	background: skyblue;
	color: #fff;
	border: none;
	border-radius: 5px;
	-webkit-box-shadow: 0 0 3px blue;
	        box-shadow: 0 0 3px blue;
	margin-left: 35%;
}

.manageItem_Wrapper {
	width: 95%;
	margin: 0 auto;
}

.manageItem_title {
	width: 100%;
	height: 40px;
	line-height: 40px;
}

.manageItem_title button {
	width: 10%;
	height: 30px;
	line-height: 30px;
	text-align: center;
	background: skyblue;
	color: #fff;
	border: none;
	border-radius: 5px;
	margin: 0 2%;
	font-size: 12px;
	cursor: pointer;
}

.manageItem_content {
	width: 100%;
	margin: 1%;
	border-radius: 5px;
}

.addItem_Wrapper {
	position: relative;
}

.addItem_Tips {
	display: none;
	position: absolute;
	left: 20%;
	top: 20%;
	width: 50%;
	height: 45px;
	line-height: 45px;
	color: #fff;
	font-size: 16px;
	text-align: center;
	border-radius: 5px;
}

.success_tips {
	display: block;
	background: rgba(16, 175, 238, 0.5);
}

.error_tips {
	display: block;
	background: rgba(255, 0, 0, 0.5);
}


.addItem_Wrapper ul {
	padding: 2%;
}

.addItem_Wrapper ul li{
	width: 100%;
	height: 55px;
	line-height: 55px;
	font-size: 14px;
	color: #666;
}

.addItem_Wrapper ul li label {
	display: inline-block;
	width: 20%;
	text-align: right;
	margin-right: 1%;
}

.addItem_Wrapper ul li input, .addItem_Wrapper ul li select {
	height: 40px;
	line-height: 40px;
	padding-left: 1%;
	border: none;
	border-radius: 5px;
}

.additem_inp input {
	width: 40%;
	background: #f5f5f5;
	color: #999;
}

.add_referenceValue input {
	width: 8%;
	background: #f5f5f5;
	color: #999;
}

.add_referenceValue span {
	display: inline-block;
	margin: 0 1%;
}

.add_referenceValue select {
	margin-left: 1%;
	width: 8%;
	background: #f1f1f1;
	color: #999;
}

.additem_saveBtn {
	margin: 1% 0 1% 40%;
}

.additem_saveBtn button {
	width: 15%;
	height: 35px;
	line-height: 35px;
	background: skyblue;
	color: #fff;
	border: none;
	border-radius: 5px;
}

/*体检项目管理-查看体检项目*/
.queryItem_Wrapper {
	width: 95%;
	margin: 0 auto;
	position: relative;
}

.queryItem_Wrapper>ul {
	width: 100%;
	border: 1px solid #ccc;
	border-radius: 5px;
}

.queryItem_Wrapper>ul li {
	height: 35px;
	line-height: 35px;
	border-bottom: 1px solid #ccc;
	color: #333;
	font-size: 12px;
}

.queryItem_Wrapper>ul li:first-child {
	background: skyblue;
	color: #fff;
}

.queryItem_Wrapper>ul li:last-child {
	border-bottom: none;
}

.queryItem_Wrapper>ul li span {
	display: inline-block;
	width: 20%;
	text-align: center;
	border-right: 1px solid #ccc;
}

.queryItem_Wrapper>ul li span:last-child {
	border-right: none;
}

.queryItem_Wrapper>ul li span:last-child button {
	width: 30%;
	height: 25px;
	line-height: 25px;
	border: none;
	background: skyblue;
	color: #fff;
	border-radius: 5px;
	margin-right: 5%;
	cursor: pointer;
}

.queryItem_window {
	display: none;
	position: absolute;
	top: 5%;
	left: 15%;
	border: 1px solid #ccc;
	width: 70%;
	border-radius: 5px;
	background: #fff;
	z-index: 6;
}

.queryItem_window ul {
	padding: 5% 2%;
}

.queryItem_window ul li {
	margin-bottom: 3%;
	font-size: 12px;
	color: #666;
}

.queryItem_window ul li label {
	display: inline-block;
	width: 20%;
	text-align: right;
	margin-right: 3%;
}

.queryItem_window ul li input,
.queryItem_window ul li select {
	height: 35px;
	line-height: 35px;
	background: #f1f1f1;
	color: #666;
	padding-left: 1%;
	border: none;
	border-radius: 5px;
}

.updateitem_inp input {
	width: 50%;
}

.update_referenceValue input {
	width: 10%;
}

.update_referenceValue span {
	margin: 0 2%;
}

.update_referenceValue select {
	width: 10%;
	margin-left: 3%;
}

.updateitem_saveBtn {
	margin: 5% 0 0 25%;
}

.updateitem_saveBtn button {
	width: 25%;
	height: 30px;
	line-height: 30px;
	background: skyblue;
	color: #fff;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	margin-right: 3%;
}

.updateitem_saveBtn button:last-child {
	background: #f5f5f5;
	color: #666;
}

.queryItem_tips {
	display: none;
	position: absolute;
	top: 60%;
	left: 15%;
	width: 55%;
	height: 40px;
	border-radius: 5px;
}

.marageOrder_Wrapper {
	width: 90%;
	margin: 0 auto;
}

.marageOrder_Wrapper ul {
	border: 1px solid #ccc;
	border-radius: 5px;
}

.marageOrder_Wrapper ul li {
	height: 35px;
	line-height: 35px;
	font-size: 12px;
	color: #666;
}

.marageOrder_Wrapper ul li:first-child {
	background: #f5f5f5;
	color: #999;
}

.marageOrder_Wrapper ul li span {
	display: inline-block;
	width: 20%;
	border-right: 1px solid #ccc;
	text-align: center;
}

.marageOrder_Wrapper ul li span:last-child {
	border-right: none;
}

.marageOrder_Wrapper ul li span:last-child button {
	width: 35%;
	height: 30px;
	line-height: 30px;
	background: skyblue;
	color: #fff;
	border: none;
	border-radius: 5px;
	cursor: pointer;
}





