/*普通用户操作界面*/
.Domestic_UpdateMess_wrapper {
	width: 96%;
	height: 96%;
	padding: 2%;
	background: rgba(36, 68, 233, .1);
}

.Domestic_UpdateMess_wrapper ul {
	width: 100%;
}

.Domestic_UpdateMess_wrapper ul li {
	width: 100%;
	margin: 2% 0;
}

.Domestic_UpdateMess_wrapper ul li label {
	display: inline-block;
	width: 10%;
	height: 45px;
	line-height: 45px;
	font-size: 12px;
	color: #333;
	text-align: right;
}

.Domestic_UpdateMess_wrapper ul li input[type='text'],
.Domestic_UpdateMess_wrapper ul li input[type='password'],
.Domestic_UpdateMess_wrapper ul li input[type='phone'],
.Domestic_UpdateMess_wrapper ul li input[type='email'],
.Domestic_UpdateMess_wrapper ul li textarea {
	width: 50%;
	height: 40px;
	line-height: 40px;
	padding-left: 1%;
	border: none;
	background: #fff;
	color: #333;
	margin-left: 3%;
}

.Domestic_UpdateMess_wrapper ul li textarea {
	height: 60px;
}

.D_UpdateSaveBtn {
	width: 30%;
	height: 40px;
	background: skyblue;
	color: #fff;
	border: none;
	border-radius: 5px;
	font-size: 20px;
	margin-left: 30%;
}


/*普通用户查看个人信息*/
.Domestic_QueryMess_wrapper {
	width: 100%;
	position: relative;
	background: rgba(36, 68, 233, .1);
}

.Domestic_QueryMess_wrapper ul {
	width: 100%;
	border: 1px solid #ccc;
}

.Domestic_QueryMess_wrapper ul li {
	font-size: 14px;
	color: #333;
	border-bottom: 1px solid #ccc;
	overflow: hidden;
	height: 45px;
	line-height: 45px;
}
.Domestic_QueryMess_wrapper ul li:last-child {
	border-bottom: none;
}

.Domestic_QueryMess_wrapper ul li label {
	padding-left: 10%;
	float: left;
	width: 20%;
}

.Domestic_QueryMess_wrapper ul li span {
	float: left;
	width: 50%;
}

.Domestic_showTips {
	display: none;
	width: 50%;
	height: 50px;
	line-height: 50px;
	font-size: 26px;
	color: #fff;
	background: rgba(13, 170, 249, 0.5);
	text-align: center;
	position: absolute;
	top: 50%;
	left: 50%;
	margin-left: -25%;
	margin-top: -25px;
}

/*教职工体检信息查看*/
.CheckExaminateWrapper {
	width: 90%;
	margin: 0 auto;
	position: relative;
}

.Worker_check_itemslist {
	width: 100%;
}

.Worker_check_itemslist ul {
	border: 1px solid #ccc;
	border-radius: 5px;
}

.Worker_check_itemslist ul li {
	height: 35px;
	line-height: 35px;
	border-bottom: 1px solid #ccc;
	font-size: 12px;
	color: #666;
}
.Worker_check_itemslist ul li:last-child {
	border-bottom: none;
}

.Worker_check_itemslist ul li:first-child {
	font-family: arial;
	color: #fff;
	background: skyblue;
}

.Worker_check_itemslist ul li span {
	display: inline-block;
	width: 16%;
	text-align: center;
	font-size: 12px;
	border-right: 1px solid #ccc;
}

.Worker_check_itemslist ul li:first-child span {
	border-right-color: #999;
}

.Worker_check_itemslist ul li span:last-child {
	border-right: none;
}

.Worker_check_itemslist ul li span button {
	width: 50%;
	height: 30px;
	line-height: 30px;
	background: #f5f5f5;
	color: #666;
	text-align: center;
	border: none;
	border-radius: 5px;
	cursor: pointer;
}

.examinateDetailWindow {
	width: 100%;
	background: #fff;
	position: absolute;
	top: 20%;
	border: 1px solid #ccc;
	padding-bottom: 5%;
	z-index: 10;
	border-radius: 6px;
	display: none;
}

.examinateDetailWindow h4 {
	width: 100%;
	height: 40px;
	line-height: 40px;
	text-align: center;
	overflow: hidden;
}

.examinateDetailWindow h4 span {
	float: left;
	margin-left: 40%;
	font-size: 20px;
}

.examinateDetailWindow h4 b {
	float: right;
	width: 40px;
	height: 25px;
	line-height: 25px;
	background: #f00;
	color: #fff;
	cursor: pointer;
	margin: 9px 2%;
}

.examinateDetailWindow ol {
	width: 90%;
	margin: 10px auto;
	border: 1px solid #ccc;
}

.examinateDetailWindow ol li {
	height: 30px;
	line-height: 30px;
	font-size: 12px;
	color: #666;
	border-bottom: 1px solid #ccc;
}

.examinateDetailWindow ol li:last-child {
	border-bottom: none;
}

.examinateDetailWindow ol li:first-child {
	background: #f5f5f5;
	color: #666;
	font-size: 16px;
	font-family: cursive;
	height: 35px;
	line-height: 35px;
}

.examinateDetailWindow ol li span {
	display: inline-block;
	width: 25%;
	text-align: center;
	border-right: 1px solid #ccc;
}

.examinateDetailWindow ol li span:last-child {
	border-right: none;
}

/*在线预约*/
.onlineOrderWrapper {
	width: 90%;
	margin: 0 auto;
	border: 1px solid #ccc;
	padding: 10px 0;
	position: relative;
}

.medicalPeople_Mess h3, .contactPeople_Mess h3 {
	width: 100%;
	height: 50px;
	line-height: 50px;
	font-size: 20px;
	color: #666;
	margin-left: 5%;
}

.medicalPeople_Mess ul,
.contactPeople_Mess ul {
	margin-left: 3%;
}

.medicalPeople_Mess ul li,
.contactPeople_Mess ul li {
	margin-bottom: 1%;
	color: #333;
	font-size: 12px;
}

.medicalPeople_Mess ul li:last-child,
.contactPeople_Mess ul li:last-child {
	margin-bottom: none;
}

.medicalPeople_Mess ul li label,
.contactPeople_Mess ul li label {
	display: inline-block;
	width: 15%;
	text-align: right;
	margin-right: 2%;
}

.onlineOrderWrapper .inp {
	display: inline-block;
	width: 40%;
	height: 35px;
	line-height: 35px;
	border: none;
	background: #f5f5f5;
	padding-left: 1%;
}

.medicalPeople_Mess input[name=sex_radio]{
	margin: 0 1% 0 3%;
}


.SubmitorderBtn {
	width: 20%;
	height: 35px;
	line-height: 35px;
	background: skyblue;
	color: #fff;
	text-align: center;
	border: none;
	border-radius: 5px;
	margin:2% 0 1% 40%;
	cursor: pointer;	
}

.onlineOrder_tips {
	display: none;
	width: 50%;
	height: 45px;
	line-height: 45px;
	position: absolute;
	top: 35%;
	left: 22%;
	border-radius: 5px;
	font-size: 16px;
	text-align: center;
	color: #fff;
}

.onlineOrder_SuccessTips {
	display: block;
	background: rgba(135, 206, 234, 0.8);
}

.onlineOrder_ErrorTips {
	display: block;
	background: rgba(255, 0, 0, 0.5);
}

.orderProgress_wrapper {
	width: 90%;
	margin: 0 auto;
}

.orderProgress_wrapper ul {
	border: 1px solid #ccc;
}

.orderProgress_wrapper ul li {
	border-bottom: 1px solid #ccc;
	height: 35px;
	line-height: 35px;
	color: #666;
	font-size: 12px;
}

.orderProgress_wrapper ul li:first-child {
	height: 40px;
	line-height: 40px;
	background: skyblue;
	color: #fff;
	font-size: 14px;
}


.orderProgress_wrapper ul li:last-child {
	border-bottom: none;
}

.orderProgress_wrapper ul li span {
	display: inline-block;
	width: 20%;
	border-right: 1px solid #ccc;
	text-align: center;
}

.orderProgress_wrapper ul li span:last-child {
	border-right: none;
}
.CheckTest_search {
	width: 100%;
	height: 40px;
	line-height: 40px;
}

.CheckTest_search input {
	float: left;
	width: 30%;
	height: 30px;
	border: 1px solid #ccc;
	border-radius: 5px 0 0 5px;
	border-right: none;
	padding-left: 1%;
}

.CheckTest_search button {
	float: left;
	width: 7%;
	height: 30px;
	background: #f5f5f5;
	color: #666;
	text-align: center;
	line-height: 30px;
	border: 1px solid #ccc;
	border-radius: 0 5px 5px 0;
}



